*{
    margin: 0;
    padding: 0;
   box-sizing: border-box;
   text-decoration: none;
   color: black;
}

 body{
font-family: 'Mulish', sans-serif;


} 

.container{
  margin-top: 0;
    max-width: 1200px;
    margin: 0 auto;
}




.none{
  display: none;
}

.actives{
  display: block;
}





::-webkit-scrollbar {
  width: 6px;
}


 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #c0c0c0; 

}

@media only screen and (max-width: 400px) {

.container{
    max-width: 320px;
    display: flex;
    flex-direction: column;


    
}



}
