
.navbar{
    display: flex;
    width: 100vw;
    padding: 10px 0;
    align-items: center;
    position: fixed;
    top: 0;
    z-index: 100;
background-color: var(--bg);


}


.logo{
    display: flex;
    font-size: 34px;
    align-items: center;
    align-items: flex-end;
    margin-right: 10px;
    color: var(--col);
.bxl-youtube{
    padding-right: 1px;
    color: red;
    border-radius: 20px;
    font-size: 40px;
}
}
.bars{
    background-color: transparent;
    border: none;
    .fa-bars{
        font-size:25px;
        margin-left: 15px;
        color: var(--col);
    }
    
}

.logo{
    margin-left: 40px;
}
.input__wrapper{
    display: flex;
    width: 600px;
    position: relative;
}

.fa-search{
    font-size: 20px;

    position: absolute;
    right: 15px;
    top: 10px;
}
.search{
    margin-left: 60px;
    height: 44px;
    width: 550px;
    border-radius: 22px;
    background: #EBEBEB;
    padding: 5px 20px;
    outline: none;
    border: none;
}

.search::placeholder{
    color: #000000;
    
}


.video{
    position: absolute;
    right: 210px;
    border: none;
    background-color: transparent;
    
}
.square{
    position: absolute;
    right: 170px;
    border: none;
    background-color: transparent;
    
}
.bell{
    position: absolute;
    right: 120px;
    font-size: 30px;
    opacity: 28%;
    background-color: transparent;
    border: none;
    color: white;
}

.avatars{
    height: 40px;
    width: 40px;
    border-radius:50%;
    object-fit: cover;
    position: absolute;
    right: 50px;
}
.select__theme{
    background-color: var(--bg);
    border: none;
    font-size: 20px;
}
.setting{
    position: sticky;
    top: 95%;
padding-left: 15px;
    background-color: transparent;
    border: none;
    color: var(--col);
    display: flex;
    align-items: center;
  i{
      color: var(--col);
      font-size: 25px;
  }
}

.sidebar{
    height: 90vh;
    display: flex;
    flex-direction: column;
    top: 64px;
    position: fixed;
    left: 0;
    z-index: 150;
background-color: var(--bg);
    overflow-y: scroll;
    
 
    
    .header__lin{
        padding:20px 10px;
        margin-top: 10px;
        color:var(--col)
        
    }
}


.more_btn{
    background-color: transparent;
    border: none;
    outline: none;
    margin: 20px 0 0 10px;
    font-family: 'Mulish', sans-serif;
font-weight: 400;
color: var(--col);


}
.subscrip__wrapper{
    background-color: var(--bg);
    border: none;
    top: 20px;
    position: relative;
    padding-bottom: 30px;
.subscrip{
    color: var(--col);
    
    padding: 0 0 20px 20px;
    font-family: 'Mulish', sans-serif;
font-size: 18px;
font-weight: 700;


}
.subscript{
    padding: 5px 0;

    .header__lin{

 align-items: center;
font-family: 'Mulish', sans-serif;
font-weight: 400;
padding-bottom: 7px;
img{
    margin:10px 10px 0 0;
}
    
    }
   
}

}

.header__link{
    margin-top: 10px;
    text-decoration: none;
    padding: 0 10px;
    border-radius: 4px;
    color: #898989;

i{
    color: var(--color);
    font-size: 30px;
    padding-right: 10px;
}
      span{
        display: none;
  }
    
}
.header__link-active{
    color: #FF0000;
    

} 



select{
    outline: none;
    bottom: none;
    margin-left: 200px;
}
.ava{
    height: 26px;
width: 26px;
border-radius: 50%;
}

.dark{  
    background-color: #273349;
    --bg: rgb(63, 63, 87);
    --col: white;
    color: white;
}
.light{
    background-color: #f2f2f2;
    --col: black;
    color: black;
    --bg: rgb(214, 214, 214);
}



::-webkit-scrollbar {
  width: 3px;
}


 
