
    .container{
        width: 1400px;
    margin-top: 64px;
    position: relative;
    top: 5px;
    .image{
        background-image: url(../../img/Cover.png);
background-position: center;
background-repeat: no-repeat;        
height: 280px;

        border-radius: 14px;
        margin-bottom: 30px;
    }
    
    .avatar__wrapper{
        display: flex;
        align-items: center;
        justify-content: space-between;
    padding-top: 20px;
    
        .avatar__wrapp{
            
            display: flex;
            align-items: center;
            .subsed{
                color: #C2C2C2;

            }
            .avatar{
                height: 80px;
                width: 80px;
                margin-right: 14px;
                
            }
        }
        .bell{
  
            border: none;
            background-color: transparent;
            
            .fa-bell{
                padding-right: 20px;
                color: #000000;
            }
        }
        
        .sub__btn{
            
            color: #ffffff;
            background: #FF0000;
            padding: 10px;
            border-radius: 20px;
            font-family: 'Mulish', sans-serif;
font-weight: 400;

        }
        .Link{
            color: #ffffff;
        }
    }
    .wrapper{
        margin-top: 20px;
        
        
        .navs{
            
            position: relative;
            
            .chanel__link{
                margin-right: 70px;
                font-family: 'Mulish', sans-serif;
                font-size: 18px;
                font-style: normal;
                font-weight: 700;
              
                
                
                
            }
            .chanel__link-active{
                color: #FF0000;
                border-bottom: 2px solid #FF0000;
            }
            .fa-search{    
                position: absolute;
                right: 360px;
                top: 0;
            }
        .text{
            color: #C2C2C2;
font-family: 'Mulish', sans-serif;
font-weight: 400;
position: absolute;
right: 0;
top: 0;

        }
        
        }
        
    }
    .video{
        position: relative;
        display: flex;
margin-top: 60px;
left: -100px;
.image{
     height: 250px;
width: 400px;
border-radius: 14px;
margin-left: 100px;
        }
        
        .text__wrapper{
            margin: 16px 32px;
            width: 448px;
            
            .video__title{
                font-family: 'Mulish', sans-serif;
font-size: 26px;
font-weight: 700;


            }

                
            }    
            
            .video__text{
                margin-top: 10px;
                width: 535px;
                color: var(--tex);
                
            }
            .video__vievs{
                color: #C2C2C2;
font-family: 'Mulish', sans-serif;
font-weight: 400;
margin-top: 32px;
            }
            

            .person__wrapper{
                list-style-type: none;

  position: absolute;
right: -175px;

                .person{
                    display: flex;
                    align-items: center;
margin-bottom: 20px;
         .person__title{
width: 200px;

                        margin-left: 20px;
                    }
                }
            }
            
        }
        
    }

    .phelps{
    font-family: 'Mulish', sans-serif;
font-size: 26px;
font-weight: 700;

top: 20px;

    }
    
    .blok__wrapper{
position: relative;
   
        display: flex;
overflow-y: hidden;


        .blok{
            margin-top: 100px;
            position: relative;
            height: 200px;
            width: 250px;
            border-radius: 14px;
            margin-right: 40px;
            
            img{
                height: 150px;
                width: 250px;
                border-radius: 14px;
                object-fit: fill;
                
            }
            .title__wrapper{
                position: relative;
                .subtitle{
                    font-family: 'Mulish', sans-serif;
                    font-weight: 700;
                    position: absolute;
                    top: 200px;
                    color: #19202C;
                    
                }
                
                
                .titls{
                    display: flex;
                    color: #C2C2C2;
                    font-family: 'Mulish', sans-serif;
                    font-size: 13px;
                    font-weight: 400;
                    
                    
                    .name{
                        text-align: right;
                        position: absolute;
                        right: 0;
                        width: 90px;
                    
                }
            }
        }
    }
    
}

.dark{  
    background-color: #273349;
    --color: rgb(255, 255, 255);
    
    --tex:rgba(255, 255, 255, 1);
}
.light{
    background-color: #f2f2f2;
    --color: black;
    --tex: rgba(0, 0, 0, 1);

}
