iframe{
    width: 250px;
    height: 150px;
    border-radius: 14px;
}
.avatar{
    height:50px;
    width: 50px;
    border-radius:50%;
    object-fit: cover;
    margin-right: 20px;
}
.title{
    margin-top: 1px;
    color: var(--color);
}
.person{
    padding-top: 10px;
    display: flex;
    align-items: center;

.rec{
    margin-left: 20px;
   color: var(--viev);
}

}
.recomend{
    list-style-type: none;

display: flex;
overflow: hidden;
overflow-x: scroll;
margin-bottom: 10px;
.bloksmall{
    padding: 20px;
    margin-right: 30px;
}

.ima{
border-radius: 50px;
    background-size: 500px 230px;
    iframe{
        margin-right: 30px;
      height: 250px;
width: 540px;
border-radius: 14px;

    }
}

}
.blok, .ima{
    background-image: url(../../img/yous.jpg);
    background-position: center;
    background-size: 240px 140px;
    border-radius: 15px;
    background-repeat: no-repeat;
}

.bloks{
    padding: 20px;
}
.titls{
    display: flex;
    justify-content: space-between;
    .views, .name{
        
        color: var(--viev);
}
.sur{
    padding-right: 40px;
}
}

.dark{  
    background-color: #273349;
    --color: white;
    color: white;
   --viev:#C2C2C2;
   --bgr: #273349;
}
.light{
    background-color: #f2f2f2;
    --color: black;
        color: black;
  --viev:#C2C2C2;
  --bgr: #f2f2f2;
}




@media only screen and (max-width: 400px) {

.recomend{
    display: flex;
    flex-direction: column;
    align-items: center;
  
}


}




::-webkit-scrollbar {
  height: 10px;
}


 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #c0c1c4; 
border-radius: 55px;

}
